.card {
  width: calc(20% - 20px);
  height: 227px;
  background: #ffffff;
  box-shadow: 0px 8px 31px rgba(28, 81, 185, 0.05);
  display: flex;
  flex-direction: column;
  cursor: pointer;
  margin: 10px 10px 26px 10px;

  @media (max-width: $breakPointTablet) {
    width: calc(33.33% - 20px);
  }

  @media (max-width: $breakPointPhone) {
    width: calc(50% - 20px);
    height: 180px;
  }
}

.card__top {
  flex: 1 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 5px;
}

.card__title {
  font-family: "Bodoni MT";
  font-style: italic;
  font-weight: 400;
  font-size: 1.75rem;
  line-height: 1.5625rem;
  text-align: center;
  color: var(--dark);

  @media (max-width: $breakPointPhone) {
    font-size: 1.5rem;
  }
}

.card__subtitle {
  font-style: normal;
  font-weight: 700;
  font-size: 1.0625rem;
  line-height: 1.5625rem;
  text-align: center;
  text-transform: uppercase;

  color: var(--grey);
}

.card:hover .card__subtitle {
  color: var(--brand);
}

.card__bottom {
  flex: 0 0 0px;
  background-color: #ff683e;
  background-blend-mode: multiply;
  background-image: url("../assets/images/look-at-this-shit.png");
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;

  font-style: normal;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.625rem;
  color: #ffffff;
  transition: all 0.1s ease-out;
  position: relative;
}

.card__bottom-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #ff683e;
  background-blend-mode: multiply;
}

.card:hover .card__bottom {
  flex: 0 0 101px;
}
