.mission {
  @media (max-width: $breakPointPhone) {
    margin: 60px 0;
  }
}

.mission-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.mission-inner__heading.heading {
  font-size: 3.375rem;
  line-height: 58px;
  text-align: center;

  @media (max-width: $breakPointPhone) {
    font-size: 2.5rem;
    line-height: 2.5rem;
  }
}

.mission-inner__sub-heading.sub-heading {
  margin-bottom: 31px;
  font-size: 2.3125rem;
  line-height: 52px;
  font-weight: 700;
  text-align: center;
  letter-spacing: 0;

  @media (max-width: $breakPointPhone) {
    font-size: 2.2rem;
    line-height: 2.5rem;

    margin-top: 10px;
  }
}

.mission__copy {
  font-style: normal;
  font-weight: 400;
  font-size: 1.125rem;
  line-height: 1.625rem;
  text-align: center;
  max-width: 905px;
  color: var(--dark);
  margin-bottom: 47px;
  letter-spacing: 0;
}
